.section.cards {
    background: $light_gray;
    padding: 75px 0 0;
}
.section.cards::after {
    width: 0;
    height: 0;
    border-style: solid;
    margin-left: -26px;
    border-width: 26px 26px 0 26px;
    border-color: $light_gray transparent transparent transparent;
    position: absolute;
    top: 100%;
    left: 50%;
    display: block;
    content: ' ';
    z-index: 2;
}
.section.cards .card .text {
    background: $light_gray;
}
.section.cards .card .text .body {
    font-size: 16px;
}
.section.cards .card:nth-child(odd) .text,
            .section.cards .card:nth-child(odd) .text h2 {
    color: $white;
    background: $blue;
}
.section.cards .card:nth-child(odd) .button {
    background: $white;
    color: $blue;
}
.section.cards .card:nth-child(even) .text,
            .section.cards .card:nth-child(even) .text h2 {
    background: $purple;
    color: $white;
}
.section.cards .card:nth-child(even) .button {
    background: $white;
    color: $purple;
}
.section.cards .card:hover:nth-child(even) .button {
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    box-shadow: 0 0 0 4px $white,
        inset 0 0 0 3px $purple;
    color: $purple;
}
.section.cards .card:hover:nth-child(odd) .button {
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    box-shadow: 0 0 0 4px $white,
        inset 0 0 0 3px $blue;
    color: $blue;
}
